.game-screen .panel {
  overflow-x: auto;
  overflow-y: scroll;
  padding: 8px;
  margin: 30px 0;
  background: #ffa32e;
  border-radius: 5px;
  border: 2px outset #ff920e;
  min-width: 50px;
}
@media screen and (orientation: portrait) {
  .game-screen .panel {
    display: flex;
    margin: 0 10px;
    width: 100%;
    overflow-x: auto;
    overflow-y: hidden;
    padding: 6px 8px;
  }
}
.game-screen .panel .block {
  background-color: white;
  border: none;
  position: relative;
  margin-bottom: 25px;
}
@media screen and (orientation: portrait) {
  .game-screen .panel .block {
    flex: 0 0 auto;
    margin-right: 10px;
    margin-bottom: 0;
    width: 40px;
    height: 40px;
  }
}
.game-screen .panel .block[data-selected]::after {
  content: '';
  position: absolute;
  top: -3px;
  left: -3px;
  right: -3px;
  bottom: -3px;
  border: 3px solid red;
  pointer-events: none;
}
.game-screen .panel .block .count {
  position: absolute;
  top: 100%;
  right: 0;
  font-family: infinity, 'Exo 2', sans-serif;
  font-weight: 900;
  color: darkred;
  pointer-events: none;
}
html.mac .game-screen .panel .block .count,
html.ios .game-screen .panel .block .count {
  font-family: 'Exo 2', infinity, sans-serif;
}
.game-screen .panel .block .count::before {
  content: 'x';
}
.game-screen .controls {
  width: 100%;
  height: 100px;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  display: flex;
  justify-content: space-around;
  align-items: center;
}
@media screen and (orientation: portrait) {
  .game-screen .controls {
    width: 300px;
    margin: 0 auto;
    position: relative;
    padding: 0 10px;
  }
}
.game-screen .controls::before {
  content: '';
  position: absolute;
  width: 100%;
  height: 70px;
  top: 15px;
  left: 0;
  background: white;
  border-radius: 12px;
  z-index: -1;
}
@media screen and (orientation: portrait) {
  .game-screen .controls::before {
    height: 60px;
    top: 20px;
  }
}
.game-screen .controls > button {
  width: 50px;
  height: 50px;
  background-size: contain;
  flex: 0 0 auto;
}
@media screen and (orientation: portrait) {
  .game-screen .controls > button {
    width: 40px;
    height: 40px;
  }
}
.game-screen .controls > button.execute,
.game-screen .controls > button.stop {
  width: 100px;
  height: 100px;
}
@media screen and (orientation: portrait) {
  .game-screen .controls > button.execute,
  .game-screen .controls > button.stop {
    width: 80px;
    height: 80px;
  }
}
.game-screen .controls > button.move {
  background-image: url('image/control-button-move.png');
}
.game-screen .controls > button.rapid {
  background-image: url('image/control-button-rapid.png');
}
.game-screen .controls > button.stop {
  background-image: url('image/control-button-stop.png');
}
.game-screen .controls > button.execute {
  background-image: url('image/control-button-execute.png');
}
.game-screen .controls > button.ranking {
  background-image: url('image/control-button-ranking.png');
}
.game-screen .statement-area {
  margin: 35px 0 0 20%;
}
.game-screen .statement-area .mission {
  padding-left: 22px;
  font-family: 'Exo 2', sans-serif;
  font-weight: 900;
  color: #2b1c79;
}
.game-screen .statement-area .statement {
  background: white;
  border: 2px solid #5651ab;
  border-right: none;
  padding: 6px 20px;
  font-size: 24px;
  border-top-left-radius: 14px;
  border-bottom-left-radius: 14px;
  color: #333;
}
